<template>
  <div v-show="disponibilidad" style="display:inline-block">
    <calculos
      banco="banamex"
      :tasa="tasa"
      :valor-del-inmueble="valorDelInmueble"
      :factor="factor"
      :factor-seguro-de-vida="factorSeguroDeVida"
      :factor-seguro-de-danos="factorSeguroDeDanos"
      :comision-administracion="comisionAdministracion"
      :iva="iva"
      :monto-de-credito="montoDeCredito"
      :monto-de-credito-por-modificacion="montoDeCreditoPorModificacion"
      :disponibilidad="disponibilidad"
      :enganche="enganche"
      :porcentaje-de-credito="porcentajeDeCreditoAsignado"
      :tipo-de-ingreso="tipoDeIngreso"

      :ochenta-banamex="mayorIgualOchenta"
    />
  </div>
</template>
<script>
import Calculos from "../Calculos.vue";

export default {
  name: "banamex",
  components: {
    Calculos
  },
  props: [
    "plazo",
    "tipoDePago",
    "valorDelInmueble",
    "productoSeleccionado",
    "montoDeCreditoPorModificacion",
    "tipoDeIngreso"
  ],
  data() {
    return {
      factorSeguroDeVida: 0.0005,
      iva: 0,

      productos: [
        {
          nombre: "Comprar mi Casa",
          porcentajeMaximoDeCredito: 0.85, // asalariados
          porcentajeMaximoDeCreditoNoAsalariados: 0.8, // otros
          plazosFijos: [true, true, true],
          // 10 15 20
          pagoFijo: {
            mayorDosMillones: {
              mayorOchenta: {
                factorInteres: [0.008041668, 0.008041668, 0.008041668],
                factorAmortizacion: [0.620311, 0.31067368, 0.1713991],
                seguroDanos: 0.0003
              },
              menorOchenta: {
                factorInteres: [0.008041668, 0.008041668, 0.008041668],
                factorAmortizacion: [0.620311, 0.31067373, 0.1713991],
                seguroDanos: 0.0003
              }
            },
            menorDosMillones: {
              mayorOchenta: {
                factorInteres: [0.00825, 0.00825, 0.008041668],
                factorAmortizacion: [0.595151515, 0.295757576, 0.1713991],
                seguroDanos: 0.0003
              },
              menorOchenta: {
                factorInteres: [0.008249999, 0.00825, 0.00825],
                factorAmortizacion: [0.595152, 0.29575758, 0.16242425],
                seguroDanos: 0.0003
              }
            }
          },
          pagoCreciente: {
            mayorDosMillones: {
              mayorOchenta: {
                factorInteres: [0, 0, 0.008041668],
                factorAmortizacion: [0, 0, 0.05823845],
                seguroDanos: 0.0003
              },
              menorOchenta: {
                factorInteres: [0, 0, 0.008041668],
                factorAmortizacion: [0, 0, 0.05823845],
                seguroDanos: 0.0003
              }
            },
            menorDosMillones: {
              mayorOchenta: {
                factorInteres: [0, 0, 0.000825],
                factorAmortizacion: [0, 0, 0.0315151516],
                seguroDanos: 0.0003
              },
              menorOchenta: {
                factorInteres: [0, 0, 0.00825],
                factorAmortizacion: [0, 0, 0.031516],
                seguroDanos: 0.0003
              }
            }
          },

          plazosCrecientes: [false, false, true],
          factoresFijos: [0.620310784, 0.310673497, 0.171398894],
          factoresCrecientes: [0, 0, 0.032331604],
          tasa: [0.099, 0.0965],
          comisionAdministracion: 0
        }
      ]
    };
  },

  computed: {
    disponibilidad() {
      var disponibilidadDePlazo = false;
      var disponibilidadDeMontoDeCredito = true;
      var tipoDePlazos;
      if (this.tipoDePago == 0) tipoDePlazos = "plazosFijos";
      if (this.tipoDePago == 1) tipoDePlazos = "plazosCrecientes";
      if (this.productoAsignado[tipoDePlazos][this.plazo])
        disponibilidadDePlazo = true;

      if (this.montoDeCreditoPorModificacion > this.maximoDeCredito) {
        disponibilidadDeMontoDeCredito = false;
      }

      return (
        disponibilidadDePlazo &&
        this.valorNumericoDelInmueble &&
        disponibilidadDeMontoDeCredito
      );
    },
    porcentajeDeCreditoAsignado() {
      console.log(this.montoDeCreditoPorModificacion);
      if (!isNaN(this.montoDeCreditoPorModificacion)) {
        return (
          (this.montoDeCreditoPorModificacion * 100) /
          this.valorNumericoDelInmueble /
          100
        );
      } else {
        return this.porcentajeMaximoDeCredito;
      }
    },
    productoAsignado() {
      return this.productos.filter(producto => {
        return producto.nombre == this.productoSeleccionado;
      })[0]; // devolver el primer objeto para no obtener un array
    },
    tasa() {
      if (this.montoDeCredito >= 2000000) return this.productoAsignado.tasa[1];
      if (this.montoDeCredito < 2000000) return this.productoAsignado.tasa[0];
      if (
        this.montoDeCredito < 2000000 &&
        this.valorNumericoDelInmueble >= 60000000
      )
        return this.productoAsignado.tasa[1];
    },
    factor() {
      // factor de amortizacion

      var tipoDePago;
      if (this.tipoDePago == 0) tipoDePago = "pagoFijo";
      if (this.tipoDePago == 1) tipoDePago = "pagoCreciente";
      var dosMillones;
      if (this.montoDeCredito >= 2000000) dosMillones = "mayorDosMillones";
      if (this.montoDeCredito < 2000000) dosMillones = "menorDosMillones";
      var ochenta;
      if (this.porcentajeDeCreditoAsignado >= 80) ochenta = "mayorOchenta";
      if (this.porcentajeDeCreditoAsignado < 80) ochenta = "menorOchenta";

      return this.productoAsignado[tipoDePago][dosMillones][ochenta][
        "factorAmortizacion"
      ][this.plazo];
    },

    factorSeguroDeDanos() {
      var tipoDePago;
      if (this.tipoDePago == 0) tipoDePago = "pagoFijo";
      if (this.tipoDePago == 1) tipoDePago = "pagoCreciente";
      var dosMillones;
      if (this.montoDeCredito >= 2000000) dosMillones = "mayorDosMillones";
      if (this.montoDeCredito < 2000000) dosMillones = "menorDosMillones";
      var ochenta;
      if (this.porcentajeDeCreditoAsignado >= 80) ochenta = "mayorOchenta";
      if (this.porcentajeDeCreditoAsignado < 80) ochenta = "menorOchenta";

      return this.productoAsignado[tipoDePago][dosMillones][ochenta][
        "seguroDanos"
      ];
    },
    mayorIgualOchenta() {
      if (this.porcentajeDeCreditoAsignado >= 80) return true;
      if (this.porcentajeDeCreditoAsignado < 80) return false;
    },
    comisionAdministracion() {
      return this.productoAsignado.comisionAdministracion;
    },
    valorNumericoDelInmueble() {
      return parseFloat(this.valorDelInmueble);
    },
    porcentajeMaximoDeCredito() {
      if (this.tipoDeIngreso != "Asalariado") {
        return this.productoAsignado.porcentajeMaximoDeCreditoNoAsalariados;
      } else {
        return this.productoAsignado.porcentajeMaximoDeCredito;
      }
    },
    maximoDeCredito() {
      return (
        this.valorNumericoDelInmueble *
        this.productoAsignado.porcentajeMaximoDeCredito
      );
    },
    montoDeCredito() {
      return this.valorNumericoDelInmueble * this.porcentajeDeCreditoAsignado;
    },
    enganche() {
      return this.valorNumericoDelInmueble - this.montoDeCredito;
    }
  }
};
</script>
